body, textarea, input, select {
  background: 0;
  border-radius: 0;
  margin: 0;
  font: 16px sans-serif;
}

.smooth {
  transition: all .2s;
}

.btn, .nav a {
  text-decoration: none;
}

.container {
  width: auto;
  margin: 0 20px;
}

label > * {
  display: inline;
}

