  margin-bottom: 10px;
  display: block;
}

.btn {
  color: #fff;
  cursor: pointer;
  background: #999;
  border: none;
  border-radius: 6px;
  margin: 2px 0;
  padding: 12px 30px 14px;
  display: inline-block;
}

.btn:hover {
  background: #888;
