}

.btn-c:hover {
  background: #c22;
}

.btn-c:active, .btn-c:focus {
  background: #b22;
}

.btn-sm {
  border-radius: 4px;
  padding: 10px 14px 11px;
}

.row {
  margin: 1% 0;
  overflow: auto;
}

.col {
  float: left;
}

.table, .c12 {
  width: 100%;
}

.c11 {
  width: 91.66%;
}

.c10 {
