}

h1 {
  font-size: 3em;
}

.btn, h2 {
  font-size: 2em;
}

.ico {
  font: 33px Arial Unicode MS, Lucida Sans Unicode;
}

.addon, .btn-sm, .nav, textarea, input, select {
  outline: none;
  font-size: 14px;
}

textarea, input, select {
  border: 1px solid #ccc;
  padding: 8px;
}

textarea:focus, input:focus, select:focus {
  border-color: #5ab;
}

textarea, input[type="text"] {
  -webkit-appearance: none;
  width: 13em;
}

.addon {
  padding: 8px 12px;
  box-shadow: 0 0 0 1px #ccc;
}

.nav, .nav .current, .nav a:hover {
  color: #fff;
  background: #000;
}

.nav {
  height: 24px;
  padding: 11px 0 15px;
}

.nav a {
  color: #aaa;
  padding-right: 1em;
  position: relative;
  top: -1px;
}

.nav .pagename {
  font-size: 22px;
  top: 1px;
}

.btn.btn-close {
  float: right;
  background: #000;
  margin: -54px 7px;
  font-size: 25px;
  display: none;
}

@media (min-width: 1310px) {
  .container {
    width: 1270px;
    margin: auto;
  }
}

@media (max-width: 870px) {
  .row .col {
    width: 100%;
  }
}

@media (max-width: 500px) {
  .btn.btn-close {
    display: block;
  }

  .nav {
    overflow: hidden;
  }

  .pagename {
    margin-top: -11px;
  }

  .nav:active, .nav:focus {
    height: auto;
  }

  .nav div:before {
    content: "";
    float: right;
    height: 4px;
    width: 20px;
    background: #000;
    border-top: 3px solid;
    border-bottom: 10px double;
    position: relative;
    top: 14px;
    right: 3px;
  }

  .nav a {
    width: 50%;
    padding: .5em 0;
    display: block;
  }
}

.table th, .table td {
  text-align: left;
  padding: .5em;
}

.table tbody > :nth-child(2n-1) {
  background: #ddd;
}

.msg {
  background: #def;
  border-left: 5px solid #59d;
  padding: 1.5em;
}

.spinner:before {
  content: "";
  box-sizing: border-box;
  width: 100px;
  height: 100px;
  border: 10px solid #ccc;
  border-top-color: #000;
  border-radius: 50%;
  margin-top: -10px;
  margin-left: -10px;
  animation: spinner .8s linear infinite;
  position: absolute;
  top: 50%;
  left: 50%;
}

@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}

#runtime_json, #flashes {
  display: none;
}

.msg {
  z-index: 1000;
  color: #000;
  width: 50%;
  opacity: .9;
  margin: 0 auto;
  padding: 10px;
  font-size: 16pt;
  display: none;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
}

.msg li {
  list-style-type: none;
}

.dt-container {
  width: 100%;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;
  display: block;
  position: relative;
  overflow-x: auto;
  overflow-y: hidden;
}

.dt-container .dt-message {
  text-align: center;
}

.dt-container .dt-loading {
  width: 100%;
  height: 40px;
  text-align: center;
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0, rgba(255, 255, 255, .9) 25%, rgba(255, 255, 255, .9) 75%, rgba(255, 255, 255, 0) 100%);
  justify-content: center;
  align-items: center;
  margin-top: -20px;
  margin-left: -50%;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
}

.dt-top, .dt-bottom {
  justify-content: space-between;
  padding: 8px 10px;
  display: flex;
}

.dt-top .dt-selector, .dt-bottom .dt-selector {
  padding: 6px;
}

.dt-top .dt-info, .dt-bottom .dt-info {
  margin: 7px 0;
}

.dt-input {
  padding: 6px 12px;
}

.dt-pagination ul {
  margin: 0;
  padding-left: 0;
}

.dt-pagination ul li {
  float: left;
  list-style: none;
}

.dt-pagination a, .dt-pagination span {
  float: left;
  color: inherit;
  border: 1px solid rgba(0, 0, 0, 0);
  margin-left: 2px;
  padding: 6px 12px;
  text-decoration: none;
  position: relative;
}

.dt-pagination a:hover {
  background-color: #d9d9d9;
}

.dt-pagination .active a, .dt-pagination .active a:focus, .dt-pagination .active a:hover {
  cursor: default;
  background-color: #d9d9d9;
}

.dt-pagination .dt-ellipsis span {
  cursor: not-allowed;
}

.dt-pagination .disabled a, .dt-pagination .disabled a:focus, .dt-pagination .disabled a:hover {
  cursor: not-allowed;
  opacity: .4;
}

.dt-pagination .pager a {
  font-weight: bold;
}

.dt-table {
  max-width: 100%;
  width: 100%;
  border-spacing: 0;
}

.dt-table > tbody > tr > td, .dt-table > tbody > tr > th, .dt-table > tfoot > tr > td, .dt-table > tfoot > tr > th, .dt-table > thead > tr > td, .dt-table > thead > tr > th {
  vertical-align: top;
  white-space: nowrap;
  padding: 8px 10px;
}

.dt-table > thead > tr > th, .dt-table > thead > tr > td {
  vertical-align: bottom;
  text-align: left;
  border-bottom: 1px solid #d9d9d9;
}

.dt-table > tfoot > tr > th, .dt-table > tfoot > tr > td {
  vertical-align: bottom;
  text-align: left;
  border-top: 1px solid #d9d9d9;
}

.dt-table th {
  vertical-align: bottom;
  text-align: left;
}

.dt-table th.dt-sorter {
  cursor: pointer;
  padding-right: 20px;
  position: relative;
}

.dt-table th.dt-sorter:before, .dt-table th.dt-sorter:after {
  content: "";
  height: 0;
  width: 0;
  opacity: .2;
  border-left: 4px solid rgba(0, 0, 0, 0);
  border-right: 4px solid rgba(0, 0, 0, 0);
  position: absolute;
  right: 7px;
}

.dt-table th.dt-sorter:before {
  border-top: 4px solid #000;
  top: 18px;
}

.dt-table th.dt-sorter:after {
  border-top: 4px solid rgba(0, 0, 0, 0);
  border-bottom: 4px solid #000;
  bottom: 22px;
}

.dt-table th.dt-sorter.asc:after, .dt-table th.dt-sorter.desc:before {
  opacity: .6;
}

